import Link from 'next/link';
import Image from 'next/image';
import { NavItem } from '../../types/business';

export default function MainHeader() {
  const navItems: NavItem[] = [
    { icon: 'home', label: 'Inicio' },
    { icon: 'network-wired', label: 'Mi red' },
    { icon: 'store', label: 'Mi negocio', active: true },
    { icon: 'envelope', label: 'Mensajes' },
    { icon: 'bell', label: 'Notificaciones' }
  ];

  return (
  <header className="bg-white border-b border-[#e5e7eb] sticky top-0 z-[1000] shadow-sm">
    <div className="header-container flex justify-between items-center max-w-[1200px] mx-auto px-6 h-16">
        <Link href="/" className="logo text-[1.75rem] font-extrabold flex items-center tracking-tight bg-gradient-to-r from-[var(--primary)] to-[var(--accent)] bg-clip-text text-transparent transition-[var(--transition)] hover:scale-[1.02]">
          <span>GO</span>
          <span>tip</span>
        </Link>
        
        <div className="search-bar flex-[0.6] mx-6 relative">
          <input 
            type="text" 
            placeholder="Buscar emprendimientos, personas o ideas..." 
            className="w-full py-[0.7rem] px-4 pl-10 rounded-full border border-[var(--border)] bg-[#f1f3f5] text-sm transition-[var(--transition)] shadow-inner focus:bg-white focus:border-[var(--primary)] focus:outline-none focus:shadow-[0_0_0_3px_rgba(67,97,238,0.2)]"
          />
          <div className="absolute left-4 top-1/2 transform -translate-y-1/2 text-[var(--text-gray)] opacity-70">
            <i className="fas fa-search text-sm"></i>
          </div>
        </div>
        
        <div className="nav-icons flex gap-1">
          {navItems.map((item, index) => (
            <Link 
              key={index}
              href="#" 
              className={`nav-icon flex flex-col items-center justify-center py-3 px-4 rounded-lg text-xs min-w-[70px] relative transition-[var(--transition)] ${
                item.active 
                  ? 'text-[var(--primary)] after:content-[""] after:absolute after:bottom-0 after:left-1/2 after:transform after:-translate-x-1/2 after:w-1/2 after:h-[3px] after:bg-[var(--primary)] after:rounded-t-sm'
                  : 'text-[var(--text-gray)] hover:bg-[rgba(67,97,238,0.08)] hover:text-[var(--primary)]'
              }`}
            >
              <i className={`fas fa-${item.icon} text-[1.25rem] mb-[0.3rem]`}></i>
              <span>{item.label}</span>
            </Link>
          ))}
        </div>
        
        <div className="user-profile flex items-center gap-3 py-2 px-3 rounded-full cursor-pointer transition-[var(--transition)] hover:bg-[rgba(67,97,238,0.08)] ml-2">
          <Image 
            src="https://randomuser.me/api/portraits/men/32.jpg" 
            alt="Perfil" 
            width={36} 
            height={36} 
            className="rounded-full border-2 border-[#e9ecef] object-cover transition-[var(--transition)] hover:border-[var(--primary)]"
          />
          <span className="text-sm font-medium">Mi perfil <i className="fas fa-caret-down ml-1"></i></span>
        </div>
      </div>
    </header>
  );
}